<template>
    <div class="admin-settings-infofields">
        <h2>Редагування вспливаючих підказок</h2>
        <div :key="field._id" v-for="(field, index) in infofieldsList" class="info-field">
            <input type="text" placeholder="Русское описание" v-model="infofieldsList[index].ru">
            <input type="text" placeholder="Опис українською" v-model="infofieldsList[index].ua">
            <button @click="updateInfoField(infofieldsList[index].ru, infofieldsList[index].ua, field._id)">Оновити</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            infofieldsList: [] // container for infofields data that obtains from server
        }
    },
    created() {
        this.getInfoFields();
    },
    methods: {
        getInfoFields() {
            // gets all infofields from server

            axios.get('/admin/getInfoFields')
                 .then(res => this.infofieldsList = res.data)
                 .catch(err => console.log(err));
        },
        updateInfoField(ru, ua, id) {
            // updates infofields with new data
            
            axios.post(`/admin/updateInfoField/${id}`, {ru: ru, ua: ua})
                 .then(res => alert('Поле оновлено'))
                 .catch(err => alert('Поле не вдалося оновити'));
        }
    }
}
</script>

<style lang="scss" scoped>
.admin-settings-infofields {
    h2 {
        margin-bottom: 2rem;
    }

    .info-field {
        input {
            margin-right: 2rem;
        }
        margin-bottom: 2rem;
    }
}
</style>
